<template>
  <div class="main-right">
    <div class="nav-content-wrap">
      <div class="nav-content-item active">
        <div class="slide-side">
          <div class="audio-wrap">
            <span class="demo">你可以随意说一句5秒钟左右的语音，例如：<br />“ RDG技术嘉年华，很好很强大！“</span>
            <div id="audio-wave" style="position:relative">
              <img style="position: absolute;top: 110px;left: 50%;transform:translateX(-50%)" src="./img/voice.png" />
              <canvas ref="myCanvas" id="myCanvas" width="298px" height="230px"></canvas>
            </div>
            <button class="audio-button" ref="audio" @click="doAstOperate()"><i class="el-icon-video-play" style="margin-right:5px;"></i>开始录音</button>
          </div>
        </div>
        <div class="text-audio">
          <el-input type="textarea" :rows="15" v-model="content" :placeholder="placeholder" clearable></el-input>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Recorder from './js/HZRecorder.js'
import CryptoJS from 'crypto-js'
import VConsole from 'vconsole'
export default {
  data() {
    // 注意： datta 有冗余字段，可以无视
    return {
      // 样式控制
      isNone: true,
      issDisplay: 'block',
      dialogVisible: false,
      // 插件
      bars: '',
      canvas: null,
      ctx: null,
      // 定时器
      drawTimer: '', // 定时器需要关闭
      // intervalKey: '', // 定时器需要关闭
      timer: '', // 获取时间定时器
      // HZRecorder.js录音控件
      recorder: '',
      time: 0,
      // 自定义参数，显示转写结果
      rawHtml: '',
      sentence: '',
      tmpHtml: '',
      timeStr: '00:00:00',
      // websocket
      socket: null, // websocket链接需要关闭
      config: {},
      bootSvcAddr: '',
      languages: [],
      language: '',
      counts: 0,
      samples: [{ label: '16k16bit' }],
      sample: '16k16bit',
      encodings: [{ label: 'raw' }],
      encode: 'raw',
      appName: '实时转写演示',
      domain: '',
      istLanguage: '',
      accent: '',
      content: '',
      placeholder: '',
      isPlay: false,
      isPlayRecord: false,
      feature: '',
      context: null,
      buffer: [],
      isPause: false,
      isReplay: false,
      hasSrc: false,
      hasfeature: false,
      sampleRate: 16000,
      param: {},
    }
  },
  created() {
    if (this.socket !== null) {
      this.socket.close()
      this.socket = null
    }
    new VConsole()
    navigator.getUserMedia = ( navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia || navigator.mediaDevices.getUserMedia)
    console.log('v2')
    console.log(navigator.getUserMedia)
    navigator.mediaDevices.enumerateDevices().then(devices => {
      // devices.forEach(v => {
      //   console.log(v.kind + ": " + v.label + " id = " + v.deviceId)
      // })
      let mics = devices.filter( device => {
        return device.kind === 'audioinput'
      })
      console.log(mics[0])
    }).catch(() => {
      console.log('navigator.mediaDevices.enumerateDevices error')
    })
  },
  // mounted: function() {},
  beforeDestroy() {},
  components: {},
  methods: {
    // 录音操作
    doAstOperate() {
      if (/开始录音/.test(this.$refs.audio.innerText)) {
        this.start()
      } else {
        this.stop()
        this.$refs.audio.innerHTML = '<i class="el-icon-video-play" style="margin-right:5px;"></i>开始录音'
      }
    },
    start() {
      this.$refs.audio.innerHTML = '<i class="el-icon-video-pause" style="margin-right:5px;"></i>结束录音'
      // 样式控制
      this.isNone = false
      this.issDisplay = 'none'
      // 开始绘制时间和音量条
      this.getTimer()
      this.initDraw()
      this.startDraw()
      // 开始录音
      this.doRecord()
    },
    stop() {
      this.rawHtml = ''
      this.recorder.getBase((audio) => {
        this.param = audio
        this.play()
        // 清空定时器
        clearInterval(this.timer)
        // clearInterval(this.intervalKey)
        clearInterval(this.drawTimer)
        this.initDraw()
        // 样式控制
        this.isNone = true
        this.issDisplay = 'block'
        this.sentence = ''
      })
    },
    doRecord() {
      Recorder.get((rec) => {
        this.recorder = rec
        this.recorder.start()
      })
    },
    play() {
      this.initWs()
    },
    pause() {
      this.$refs.audios.pause()
      this.$refs.audios.currentTime = 0.0
      this.isPause = false
    },

    // 传递录音数据，调用服务
    initWs() {
      if (typeof WebSocket === 'undefined') {
        this.$message.error('您的浏览器不支持websocket')
      } else {
        let date = new Date().toGMTString()
        let authorization = this.getAuth(date, 'GET', '/createRec', '36.7.109.47')
        this.socket = new WebSocket(`ws://36.7.109.47:8685/createRec?authorization=${authorization}&host=172.31.65.239&date=${date}`)
        this.socket.onopen = this.wsOpen
        this.socket.onerror = this.wsError
        this.socket.onmessage = this.wsMessage
        this.socket.onclose = this.wsClose
      }
    },
    wsSend() {
      this.socket.send(
        JSON.stringify({
          data: this.param,
          appid: 'dIf4XLly',
          scene: 'admin.extractDataVoca',
          debug: true,
        })
      )
    },
    wsOpen() {
      this.wsSend()
    },
    wsError() {
      this.socket.close()
    },
    wsMessage(msg) {
      let res = JSON.parse(msg.data)
      this.content = this.content + JSON.stringify(res.result)
    },
    wsClose() {
      this.socket.close()
    },
    initDraw() {
      this.canvas = document.getElementById('myCanvas')
      this.ctx = this.canvas.getContext('2d')
      this.bars = Array(23).fill(5)
      // 先清除 在绘制
      this.ctx.clearRect(0, 0, this.canvas.clientWidth, this.canvas.clientHeight)
      this.drawBars()
      this.drawTime('00:00:00')
    },
    startDraw() {
      var that = this
      this.drawTimer = setInterval(function() {
        // 先清除 在绘制
        that.ctx.clearRect(0, 0, that.canvas.clientWidth, that.canvas.clientHeight)
        that.drawBars()
        that.setBar()
        that.drawTime(that.timeStr)
      }, 120)
    },
    drawBars() {
      for (var i = 0; i < this.bars.length; i++) {
        var height = this.bars[i]
        var color = height > 5 ? '#fff' : 'rgba(255,255,255,0.48)'
        this.ctx.fillStyle = color
        this.ctx.fillRect(20 + i * 4, this.alignCenter(height), 2, height)
        this.ctx.fillRect(298 - 20 - i * 4, this.alignCenter(height), 2, height)
      }
    },
    setBar() {
      var average = Math.random() * 20 + 5
      this.bars.push(average)
      this.bars.shift()
    },
    getTimer() {
      var that = this
      that.time = 0
      that.timeStr = '00:00:00'
      that.timer = setInterval(function() {
        that.time += 1
        var h = parseInt(that.time / 3600) < 10 ? '0' + parseInt(that.time / 3600) : parseInt(that.time / 3600)
        var last__ = that.time % 3600
        var m = parseInt(last__ / 60) < 10 ? '0' + parseInt(last__ / 60) : parseInt(last__ / 60)
        var s = that.time % 60 < 10 ? '0' + parseInt(that.time % 60) : parseInt(that.time % 60)
        that.timeStr = '' + h + ':' + m + ':' + s
      }, 1000)
    },
    drawTime(timeStr) {
      this.ctx.font = '14px bold 黑体'
      this.ctx.fillStyle = '#fff'
      this.ctx.fillText(timeStr, (300 - 38) / 2 - 11, ((230 - 56) * 3) / 5 + 78)
    },
    alignCenter(height) {
      return ((this.canvas.clientHeight - height) * 3) / 5
    },
    checksum(chars) {
      var sum = 0
      for (var i = 0; i < chars.length; i++) {
        var c = chars.charCodeAt(i)
        if (!(c >= 0x0001 && c <= 0x007e) && !(0xff60 <= c && c <= 0xff9f)) {
          sum += 1
        }
      }
      return sum
    },
    getAuth(date, type, Service_ID, host) {
      //  let api_key="0f7e02e945223afdfaa39efec0349802"
      let api_key = 'dIf4XLly'
      let algorithm = 'hmac-sha256'
      let headers = 'host date request-line'
      // let signature_origin = 'host: ' + host + '\ndate: ' + date + '\n' + type + ' /v1/private/' + Service_ID + ' HTTP/1.1'
      let signature_origin = 'host: ' + host + '\ndate: ' + date + '\n' + 'GET ' + Service_ID + ' HTTP/1.1'
      //  let signature_sha = CryptoJS.HmacSHA256(signature_origin,"ZWY1ZTk3YjdlN2E5YjE5MWM5YzE4NjM4")
      let signature_sha = CryptoJS.HmacSHA256(signature_origin, 'B8A8BA025C5A4D59A51C89ECE708D42B')
      let signature = CryptoJS.enc.Base64.stringify(signature_sha)
      let authorization_origin = `hmac api_key="${api_key}", algorithm="${algorithm}", headers="${headers}", signature="${signature}"`
      let authorization = btoa(authorization_origin)
      return authorization
    },
  },
}
</script>
